	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"T Minus 6 Hours","date":"30 Nov 2007","slug":"/2007-11-30-t-minus-6-hours/","tags":"Y2007"},"html":"<p><a href=\"http://www.flickr.com/photos/51035717986@N01/2075092269\" title=\"View 'CIMG0347' on Flickr.com\"><img src=\"http://farm3.static.flickr.com/2105/2075092269_16b3b2b958.jpg\" alt=\"CIMG0347\" border=\"0\" width=\"500\" height=\"375\" /></a></p>\n<p>Officially we re-open tomorrow at 7 am, but we're hoping to have things cleaned up this afternoon so we can serve guests inside this evening.</p>\n<p>Even with all the debris in the way, several guests per hour will fight their way to the door only to be disappointed by the closed lobby. Give us a few more hours, please!</p>"},"next":{"fields":{"title":"T Minus 24 Hours","date":"29 Nov 2007","slug":"/2007-11-29-t-minus-24-hours/"}},"previous":{"fields":{"title":"We Knew You'd Be Coming So We Baked A Cake","date":"30 Nov 2007","slug":"/2007-11-30-we-knew-youd-be-coming-so-we-baked-a-cake/"}}},{"node":{"fields":{"title":"T Minus 24 Hours","date":"29 Nov 2007","slug":"/2007-11-29-t-minus-24-hours/","tags":"Y2007"},"html":"<p><a href=\"http://www.flickr.com/photos/51035717986@N01/2073533513\" title=\"View 'CIMG0605' on Flickr.com\"><img src=\"http://farm3.static.flickr.com/2004/2073533513_511e0b29de.jpg\" alt=\"CIMG0605\" border=\"0\" width=\"500\" height=\"375\" /></a></p>\n<p>It's finally starting to look like what we imagined...</p>"},"next":{"fields":{"title":"Grand Opening In The New Style","date":"27 Nov 2007","slug":"/2007-11-27-grand-opening-in-the-new-style/"}},"previous":{"fields":{"title":"T Minus 6 Hours","date":"30 Nov 2007","slug":"/2007-11-30-t-minus-6-hours/"}}},{"node":{"fields":{"title":"Grand Opening In The New Style","date":"27 Nov 2007","slug":"/2007-11-27-grand-opening-in-the-new-style/","tags":"Y2007"},"html":"<p>The past 10 days have been crazy hectic, but we can now say with 99% certainty that we will open our McCaf&eacute; this Saturday. We've been handing out coupons, we've given interviews, we'll be giving a reception for press and politicians (and a few friends) next week, and we've got an ad in the local paper...</p>\n<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2007/11/anzeige.jpg\" alt=\"anzeige.jpg\" border=\"0\" width=\"500\" height=\"762\" /></p>"},"next":{"fields":{"title":"You Can Say You To Me","date":"20 Nov 2007","slug":"/2007-11-20-you-can-say-you-to-me/"}},"previous":{"fields":{"title":"T Minus 24 Hours","date":"29 Nov 2007","slug":"/2007-11-29-t-minus-24-hours/"}}}],"pathPrefix":"","first":false,"last":false,"index":95,"pageCount":1101}}

